<template>
  <el-container v-loading="fullscreenLoading"
    class="height">
    <iframe :src='$route.query.src'
      class="iframe"
      ref="iframe">
    </iframe>
  </el-container>
</template>

<script>
export default {
  name: 'myiframe',
  data() {
    return {
      fullscreenLoading: false,
    };
  },
  mounted() {
    const { iframe } = this.$refs;
    if (iframe.attachEvent) {
      iframe.attachEvent('onload', () => {
        this.fullscreenLoading = false;
      });
    } else {
      iframe.onload = () => {
        this.fullscreenLoading = false;
      };
    }
  },
  watch: {
    '$route.query.src': {
      handler(o, n) {
        const reg = /^\/*#/;
        if (!reg.test(o) || !reg.test(n)) this.fullscreenLoading = true;
      },
    },
  },
};
</script>

<style>
.height {
  height: 100%;
}
.iframe {
  width: 100%;
  height: 100%;
  border: 0;
  overflow: hidden;
  box-sizing: border-box;
}
</style>
