<template>
  <div class="login_wrapper">
    <div class="login">
      <el-form>
        <el-form-item>
          <h2 class="title">BMS管理系统</h2>
        </el-form-item>
        <el-form-item>
          <el-input v-model="username"
            placeholder="账号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input type="password"
            v-model="password"
            placeholder="密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-col :span="11">
            <el-input v-model="code"
              placeholder="验证码"></el-input>
          </el-col>
          <el-col :span="2">&nbsp;</el-col>
          <el-col :span="11">
            <img @click="random=Math.random()"
              class="code"
              :src="codeurl+random"
              alt="验证码">
          </el-col>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"
            @click="login">登陆</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { userapi } from '@/components/api';

export default {
  name: 'login',
  data() {
    return {
      username: '',
      password: '',
      code: '',
      codeurl: '/user/getImage?d=',
      random: '',
    };
  },
  mounted() {
    document.onkeydown = (event) => {
      const e = event || window.event;
      // enter 键
      if (e && e.keyCode === 13) { this.login(); }
    };
  },
  methods: {
    async login() {
      if (await userapi.login(this.username, this.password, this.code)) {
        this.$router.push({ name: 'home' });
      } else {
        this.random = Math.random();
      }
    },
  },
};
</script>
<style lang="stylus">
@import '~nib/index.styl'
$input_width = 300px
.login_wrapper
  height 100vh
  width 100vw
  text-align center
  background linear-gradient(#53e3a6, #409EFF)
  .login
    padding-top 200px
    margin auto
    width $input_width
    .title
      color #ffffff
    .code
      height 40px
      border-radius 4px
  .el-button
    width $input_width
</style>
