<template>
  <div class="updPassword">
      <el-form :rules="rules"
        :model="password"
        ref="updPassword"
        label-width="100px">
        <el-form-item>
          <h2 class="title">重置密码</h2>
        </el-form-item>
        <el-form-item label="旧密码">
          <el-input type="password"
            v-model="password.old"
            placeholder="旧密码"></el-input>
        </el-form-item>
        <el-form-item label="新密码">
          <el-input type="password"
            v-model="password.new1"
            placeholder="新密码"></el-input>
        </el-form-item>
        <el-form-item label="确认新密码"
          prop="new2">
          <el-input type="password"
            v-model="password.new2"
            placeholder="确认新密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"
            @click="submitForm">重置密码</el-button>
        </el-form-item>
      </el-form>
  </div>
</template>

<script>
import { userapi } from '@/components/api';

export default {
  name: 'updPassword',
  data() {
    return {
      password: {
        old: '',
        new1: '',
        new2: '',
      },
      rules: {
        new2: [
          {
            validator: (rule, value, callback) => {
              if (value !== this.password.new1) {
                callback(new Error('两次输入密码不一致!'));
              }
              callback();
            },
            trigger: 'blur',
          },
        ],
      },
    };
  },
  methods: {
    submitForm() {
      this.$refs.updPassword.validate((valid) => {
        if (valid) {
          this.updPassword();
        } else {
          this.$message({
            type: 'error',
            message: '表单未验证通过!',
            duration: 500,
          });
        }
      });
    },
    async updPassword() {
      if (await userapi.updPassword(this.password.old, this.password.new1)) {
        this.$message({
          type: 'success',
          message: '密码更新成功!',
          duration: 500,
          onClose: () => this.$router.push({ name: 'home' }),
        });
      }
    },
  },
};
</script>
<style lang="stylus">
.updPassword
  width 500px
  margin auto
  .el-form
    padding-top 200px
    text-align center
</style>
