import axios from '@/components/ajax';
import NodeRSA from 'node-rsa';

const orgapi = {};
orgapi.OrgTree = async () => {
  const data = await axios.get('/org/tree');
  return data.d;
};
orgapi.OrgFindEmp = async (params) => {
  const data = await axios.get('/org/findEmp', { params });
  return data.d;
};

const menuapi = axios.apiFactory('menu');
menuapi.getSystemMenuTree = async () => {
  const data = await axios.get('/menu/getSystemMenuTree');
  return data.d;
};
menuapi.getUserSystemMenuTree = async (id) => {
  const params = id ? { id } : {};
  const data = await axios.get('/menu/getUserSystemMenuTree', { params });
  return data.d;
};

const userapi = axios.apiFactory('user');
userapi.resetPassword = async (empno) => {
  const data = await axios.put('/user/resetPassword', { }, { params: { empno } });
  return data.ec === axios.RetState.SUCCESS.ec;
};
userapi.getUserRoles = async (userId) => {
  const data = await axios.get('/user/getUserRoles', { params: { userId } });
  return data.d;
};
userapi.saveUserRole = async (userId, roleIds) => {
  const data = await axios.post('/user/saveUserRole',
    { userId, roleIds });
  return data.ec === axios.RetState.SUCCESS.ec;
};
userapi.login = async (username, password, code) => {
  const PUBLIC = await axios.get('/rsa/getPublicKey');
  const pubKey = new NodeRSA(PUBLIC.d, 'pkcs8-public');
  pubKey.setOptions({ encryptionScheme: 'pkcs1' });
  const data = await axios.get('/user/login', {
    params: {
      username,
      password: pubKey.encrypt(password, 'base64'),
      code,
    },
  });
  return data.ec === axios.RetState.SUCCESS.ec;
};
userapi.logout = async () => {
  const data = await axios.get('user/logout');
  return data.ec === axios.RetState.SUCCESS.ec;
};
userapi.updPassword = async (oldPassword, newPassword) => {
  const PUBLIC = await axios.get('/rsa/getPublicKey');
  const pubKey = new NodeRSA(PUBLIC.d, 'pkcs8-public');
  pubKey.setOptions({ encryptionScheme: 'pkcs1' });
  const data = await axios.put('/user/updPassword', {
    oldPassword: pubKey.encrypt(oldPassword, 'base64'),
    newPassword: pubKey.encrypt(newPassword, 'base64'),
  });
  return data.ec === axios.RetState.SUCCESS.ec;
};

const roleapi = axios.apiFactory('role');

const rolemenuapi = axios.apiFactory('rolemenu');
rolemenuapi.saveRoleMenuBatch = async (roleId, menuIds) => {
  const data = await axios.post('/rolemenu/saveRoleMenuBatch',
    { roleId, menuIds });
  return data.ec === axios.RetState.SUCCESS.ec;
};

const pageTemplate = { curPage: 1, pageSize: 10 };
const creatPage = () => JSON.parse(JSON.stringify(pageTemplate));

export {
  orgapi,
  menuapi,
  userapi,
  roleapi,
  rolemenuapi,
  creatPage,
};
