
<template>
  <el-submenu v-if="item.children&&item.children.length"
    :key="item.id"
    :index="item.id.toString()">
    <template slot="title">
      <i class="el-icon-" v-if="depth==0">{{ item.menuName[0] }}</i>
      <span slot="title">{{ item.menuName}}</span>
    </template>
    <tree-menus :item='item'
      v-for="item in item.children"
      :key="item.id.toString()"
      :depth="depth+1"
      @click="onclick"
      ></tree-menus>
  </el-submenu>
  <el-menu-item v-else
    :key="item.id"
    :index="item.id.toString()"
    @click="onclick(item)">
    <i class="el-icon-" v-if="depth==0">{{ item.menuName[0] }}</i>
    <span slot="title">{{ item.menuName }}</span>
  </el-menu-item>
</template>
<script>
export default {
  name: 'treeMenus',
  props: {
    item: { },
    depth: { default: 0 },
  },
  methods: {
    onclick(item) {
      this.$emit('click', item);
    },
  },
};
</script>
