import Vue from 'vue';
import Router from 'vue-router';


import home from '@/views/home/index.vue';
import iframe from '@/views/home/iframe.vue';
import login from '@/views/login/index.vue';
import updPassword from '@/views/updPassword/index.vue';

const editTree = r => require.ensure([], () => r(require('@/views/editTree/index')),
  'editTree');
const editUser = r => require.ensure([], () => r(require('@/views/editUser/index')),
  'editUser');
const editRole = r => require.ensure([], () => r(require('@/views/editRole/index')),
  'editRole');
const contractSpirit = r => require.ensure([], () => r(require('@/views/contractSpirit/index')),
  'contractSpirit');
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      children: [{
        path: '/iframe',
        name: 'iframe',
        component: iframe,
      }],
    }, {
      path: '/login',
      name: 'login',
      component: login,
    }, {
      path: '/editTree',
      name: 'editTree',
      component: editTree,
    }, {
      path: '/editUser',
      name: 'editUser',
      component: editUser,
    }, {
      path: '/editRole',
      name: 'editRole',
      component: editRole,
    }, {
      path: '/updPassword',
      name: 'updPassword',
      component: updPassword,
    }, {
      path: '/contractSpirit',
      name: 'contractSpirit',
      component: contractSpirit,
    },
    { path: '*', redirect: '/login' },
  ],
});
