import axios from 'axios';
import { Loading, Message } from 'element-ui';

const RetState = {
  SUCCESS: { ec: '0', msg: '成功' },
  FAIL: { ec: '1', msg: '失败' },
  NO_PERMISSION: { ec: '2', msg: '没有许可' },
  NEED_CACHEABLE: { ec: '3', msg: '需要缓存' },
  SESSION_TIME_OUT: { ec: '4', msg: '登录超时' },
  NO_RESET_PASSWORD: { ec: '5', msg: '没有修改密码' },
};

const loading = (function loading() {
  let IndicatorCount = 0;
  let loadingInstance;
  return {
    show: () => {
      if (IndicatorCount === 0) {
        loadingInstance = Loading.service({
          fullscreen: true,
          lock: true,
          text: 'Loading',
          background: 'rgba(0, 0, 0, 0.7)',
        });
      }
      IndicatorCount += 1;
    },
    hide: () => {
      IndicatorCount -= 1;
      if (IndicatorCount === 0) { loadingInstance.close(); }
    },
  };
}());


axios.interceptors.request.use((config) => {
  loading.show();
  return config;
});


axios.interceptors.response.use((response) => {
  loading.hide();
  switch (response.data.ec) {
    case RetState.SUCCESS.ec: break;
    case RetState.FAIL.ec:
      Message({
        message: response.data.es,
        type: 'warning',
        duration: 1000,
      });
      break;
    case RetState.SESSION_TIME_OUT.ec:
      Message({
        message: response.data.es,
        type: 'error',
        duration: 1000,
        onClose() {
          window.top.location = '#/login';
        },
      });
      break;
    case RetState.NO_RESET_PASSWORD.ec:
      Message({
        message: response.data.es,
        type: 'error',
        duration: 1000,
        onClose() {
          window.top.location = '#/updPassword';
        },
      });
      break;
    default:
      Message({
        message: `服务器返回异常编号：${response.data.ec},错误信息：${response.data.es}`,
        type: 'error',
        duration: 1000,
      });
  }
  return response.data;
}, (err) => {
  loading.hide();
  Message({
    message: err,
    type: 'error',
    duration: 1000,
  });
});

axios.RetState = RetState;

axios.apiFactory = (name) => {
  const apis = {};
  if (!apis[name]) {
    apis[name] = {
      get: async (search, page) => {
        const params = {};
        Object.keys(search || {}).forEach((item) => {
          if (search[item]) params[item] = search[item];
        });
        Object.keys(page || {}).forEach((item) => {
          if (page[item]) params[item] = page[item];
        });
        const data = await axios.get(`/${name}/list`, { params });
        return data.d;
      },
      add: async (object) => {
        const data = await axios.post(`/${name}/add`, object);
        return data.ec === axios.RetState.SUCCESS.ec;
      },
      deleteByIds: async (id) => {
        const ids = Array.isArray(id) ? id.join(',') : id;
        const data = await axios.delete(`/${name}/deleteByIds`, { params: { ids } });
        return data.ec === axios.RetState.SUCCESS.ec;
      },
      update: async (object) => {
        const data = await axios.put(`/${name}/update`, object);
        return data.ec === axios.RetState.SUCCESS.ec;
      },
    };
  }
  return apis[name];
};

export default axios;
