<template>
  <div class="home">
    <el-container>
      <el-header>
        <div class="collapse-btn"
          @click="isCollapse=!isCollapse">
          <i class="el-icon-menu"></i>
        </div>
        <div class="logo">BMS管理系统</div>
        <div class="header-right">
          <div class="header-user-con">
            <div class="btn-fullscreen"
              @click="handleFullScreen">
              <el-tooltip :content="fullscreen?`取消全屏`:`全屏`"
                placement="bottom">
                <i class="el-icon-rank"></i>
              </el-tooltip>
            </div>
            <div class="updpassword"
              @click="$router.push({ name: 'updPassword' })">
              <el-tooltip content="重置密码"
                placement="bottom">
                <i class="el-icon-setting"></i>
              </el-tooltip>
            </div>
            <div class="updpassword"
              @click="logout">
              <el-tooltip content="登出"
                placement="bottom">
                <i class="el-icon-circle-plus-outline"></i>
              </el-tooltip>
            </div>
          </div>
        </div>
      </el-header>
      <el-container>
        <el-menu background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          :collapse="isCollapse"
          unique-opened>
          <treeMenus :item='item'
            v-for="item in tree"
            :key="item.id"
            @click="handleNodeClick"></treeMenus>
        </el-menu>
        <el-main ref="main">
          <div v-if="$route.name=='home'">欢迎登陆</div>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import { menuapi, userapi } from '@/components/api';
import treeMenus from './treeMenus.vue';

export default {
  components: {
    treeMenus,
  },
  data() {
    return {
      fullscreen: false,
      isCollapse: false,
      defaultProps: {
        label: 'menuName',
        children: 'children',
      },
    };
  },
  asyncComputed: {
    async tree() {
      const data = await menuapi.getUserSystemMenuTree();
      return data.children;
    },
  },
  methods: {
    handleFullScreen() {
      const element = document.documentElement;
      if (this.fullscreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.webkitRequestFullScreen) {
        element.webkitRequestFullScreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.msRequestFullscreen) {
        // IE11
        element.msRequestFullscreen();
      }
      this.fullscreen = !this.fullscreen;
    },
    handleNodeClick(data) {
      this.$router.push({ name: 'iframe', query: { src: data.url } });
    },
    mainInit() {
      const { main } = this.$refs;
      main.$el.style.height = `${document.documentElement.clientHeight - 63}px`;
    },
    async logout() {
      if (await userapi.logout()) {
        this.$router.push({ name: 'login' });
      }
    },
  },
  mounted() {
    this.mainInit();
    window.onresize = () => {
      this.mainInit();
    };
  },
};
</script>
<style lang="stylus">
.home
  header-height = 60px
  .el-header
    background-color #409EFF
    height = header-height
    color #ffffff
    font-size 23px
    line-height header-height
    .collapse-btn
      float left
      width 45px
    .logo
      float left
    .header-right
      float right
      .header-user-con
        display flex
        div
          transform: rotate(45deg);
          margin-left 10px
  .el-aside
    border-right 1px solid #DDD
  .el-menu
    width 200px
  .el-menu--collapse
    width 64px
  .el-main
    padding 0
    overflow hidden
</style>
